//user.js 用于定义用户的登录、注册、信息、修改密码、名单、编辑修改用户信息

import req from './http.js'

//定义接口  

//在这里定义了一个登陆的接口，把登陆的接口暴露出去给组件使用，登录后可以获取用户信息
export const LOGIN =params=>req('post','/login.php',params)
//这里使用了箭头函数，转换一下写法：
//export const LOGIN=function(req){
//    return req('post','/operator/login',params)
//}

//直接根据学号获取个人信息（web）
export const GET_USER_DETAIL =params=>req('post','/get_user_info.php',params)

//定义注册接口
// export const REG =params=>req('post','/operator/reg',params)

//定义修改密码接口
export const CHANGEPWD =params=>req('post','/change_password.php',params)

//定义获取用户列表接口
export const GETLIST =params=>req('post','/get_user_list.php',params)

//编辑用户
export const EDIT =params=>req('post','/edit_info.php',params)

//删除用户
export const DEL =params=>req('post','/delete.php',params)
