<template>
    <div>
        <div class="block">
            <span>离开计时设置</span>
            <el-slider v-model="leftTime" :max="60" :show-input="true" @input="enterEditMode"></el-slider>
        </div>
        <div class="block">
            <span>最大违规次数</span>
            <el-slider v-model="violationMax"  :max="10" :step="1" :show-input="true" @input="enterEditMode"></el-slider>
        </div>
        <div class="block">
            <span>封禁天数</span>
            <el-slider v-model="banDays"  :max="5" :step="1" :show-input="true" @input="enterEditMode"></el-slider>
        </div>
        <el-row type="flex" justify="center">
            <el-tooltip class="item" effect="dark" content="确定修改" placement="bottom">
                <el-button type="success" icon="el-icon-check" :disabled="disabledEdit" circle @click="confirmEdit"></el-button>                
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="放弃修改" placement="bottom">
                <el-button type="danger" icon="el-icon-close" :disabled="disabledEdit" circle @click="cancelEdit"></el-button>
            </el-tooltip>
        </el-row>
    </div>
</template>

<script>
export default {
    name:"SetRules",
    data() {
        return {
            leftTime: 40,
            violationMax: 5,
            banDays: 3,
            disabledEdit: true
        }
    },
    methods: {
        //用户一拖动滑块，进入可以编辑的模式
        enterEditMode() {
            if(this.disabledEdit)
                this.disabledEdit = false;
        },
        confirmEdit() {
            var that = this;
            this.$confirm('是否修改?（注意此操作不可逆）', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '修改成功!'
                });
                that.disabledEdit = true;
               //这里空出来一块写向服务器发送请求修改数据
                
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消修改'
                });
            });
        },
        cancelEdit() {
            var that = this;
            //这里逻辑目前暂定为在向服务器请求原来的数据,这里直接赋值模拟
            this.leftTime = 40;
            this.violationMax = 5;
            this.banDays = 3;
            setTimeout(()=>{
                that.disabledEdit = true;
            },50);
            this.$message({
                type: 'info',
                message: '取消修改'
            });
        }
    },
}
</script>

