<template>
    <div>
        <!-- 基础信息(basicInfos)展示 -->
        <el-row :gutter="20">
            <el-col :span="6" v-for="(value, name) in basicInfos" :key="(value, name)">
                <el-card>
                    <el-row>
                        <el-col :span="12">
                            <div class="b-i-value" style="color:#34495e;">{{value[0]}}</div>
                            <div class="b-i-name">{{name}}</div>
                        </el-col>
                        <el-col :span="12" style="text-align: center;">
                            <i class="iconfont b-i-icon" :class="value[1]" :style="{color:value[2]}"></i>
                        </el-col>
                    </el-row>
                </el-card>
            </el-col>
        </el-row>
        <!-- 警报 -->
        <el-row>
            <el-card style="width: 100%;">
                <template #header>
                    <div>
                        <el-badge :value="warningInfos.length" :max="99" type="danger">
                            <span style="font-size:20px;font-weight:bold">待处理警报</span>
                        </el-badge>
                    </div>
                </template>
                <el-table
                :data="warningInfos"
                style="width: 100%"
                height="250">
                    <el-table-column prop="time" label="时间" width="180"></el-table-column>
                    <el-table-column prop="floor" label="楼层" width="120"></el-table-column>
                    <el-table-column prop="seatid" label="座位号" width="120"></el-table-column>
                    <el-table-column prop="reason" label="违规原因" width="320"></el-table-column>
                    <el-table-column label="操作">
                        <template #default="scope">
                            <el-button size="mini" type="warning" @click="handleDealWarning(scope.$index)">未处理</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-card>
        </el-row>
        <!-- 图表 -->
        <el-row>
            <el-card style="width: 100%;" shadow="hover">
                <template #header>
                    <div class="card-header">
                      <span style="font-size:20px;font-weight:bold">实时状态</span>
                    </div>
                </template>
                <div>
                    <line-chart></line-chart>
                </div>
            </el-card>
        </el-row>
    </div>
</template>

<script>
import LineChart from './LineChart.vue';
export default {
  components: { LineChart },
    name:"data-show",
    data() {
        return {
            basicInfos:{
                '当日预约总数': [1000,'icon-jinru','#eccc68'],
                '当日签退总数': [200,'icon-ren','#1abc9c'],
                '使用座位数': [240,'icon-zixishiicon','#e67e22'],
                '剩余座位数': [880,'icon-zuowei','#ff6b81']
            },
            warningInfos: [
                {
                    floor: '一楼南',
                    seatid: '001',
                    reason: '违规占座！',
                    time: '2021/4/15 20:12'
                },
                {
                    floor: '一楼东',
                    seatid: '023',
                    reason: '违规占座！',
                    time: '2021/4/15 19:39'
                },
                {
                    floor: '四楼实',
                    seatid: '101',
                    reason: '未在规定时间内到达！',
                    time: '2021/4/15 18:12'
                },
                {
                    floor: '三楼南',
                    seatid: '301',
                    reason: '违规占座！',
                    time: '2021/4/15 14:01'
                },
                {
                    floor: '五楼南',
                    seatid: '201',
                    reason: '违规占座！',
                    time: '2021/4/15 11:43'
                },
                {
                    floor: '五楼南',
                    seatid: '201',
                    reason: '违规占座！',
                    time: '2021/4/15 11:43'
                },
            ]
        }
    },
    methods: {
        handleDealWarning(index){
            this.$confirm('是否已经完成处理？', '提示', {
                confirmButtonText: '是',
                cancelButtonText: '否',
                type: 'warning'
            }).then(() => {
                this.warningInfos.splice(index,1);
                this.$message({
                  type: 'success',
                  message: '处理成功!'
                });
            });
        }
    }
}
</script>

<style scope>
.el-row {
    margin-bottom: 20px;
}
.el-row:last-child {
    margin-bottom: 0;
}

/* basicInfos' value */
.b-i-value {
    font-size: 24px;
}
/* basicInfos' name */
.b-i-name {
    font-size: 12px;
}
/* basicInfos' icon */
.b-i-icon {
    font-size: 50px;
    text-align: center;
}
/* 水平分割线 */
.divider-hor {
    margin: 10px 0;
}
</style>