<template>
    <div>
        <!-- 表格 -->
        <el-table stripe
        :data="getProcessedData().slice((currentPage-1)*10,(currentPage-1)*10+10)"  
        :default-sort = "{prop: 'id', order: 'descending'}"
        @sort-change="customSort"
        >
            <el-table-column prop="StuID" label="学号" sortable="custom" width="180"></el-table-column>
            <el-table-column prop="StuName" label="姓名" width="180"></el-table-column>
            <el-table-column prop="Password" label="密码" width="180"></el-table-column>
            <el-table-column prop="Violation" label="违规次数" sortable="custom" width="180"></el-table-column>
            <!-- 操作栏 -->
            <el-table-column>
                <template #header>
                    <el-input v-model="search" size="mini" placeholder="输入学号或者姓名搜索"/>
                </template>
                <template #default="scope">
                    <el-button size="mini" type="info" @click="handleClickDetail(scope.$index, scope.row)">详细</el-button>
                    <el-button size="mini" @click="handleClickEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button size="mini" type="danger" @click="handleClickDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-row type="flex" justify="center"> 
            <el-pagination background layout="prev, pager, next" :total="getProcessedData().length" @current-change="getCurrentPage"></el-pagination>
        </el-row>
        <!-- 编辑框 -->
        <el-dialog title="学生信息" v-model="editDialog.visible">
            <el-form>
                <el-form-item label="学号" :label-width="editDialog.labelWidth" :disabled="true">
                    <el-input v-model="editDialog.formContent.StuID" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="姓名" :label-width="editDialog.labelWidth">
                    <el-input v-model="editDialog.formContent.StuName" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="密码" :label-width="editDialog.labelWidth">
                    <el-input v-model="editDialog.formContent.Password" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="违规次数" :label-width="editDialog.labelWidth">
                    <el-input v-model="editDialog.formContent.Violation" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <template #footer>
                <span class="dialog-footer">
                <el-button @click="editDialog.visible = false">取 消</el-button>
                <el-button type="primary" @click="handleConfirmDialog">确 定</el-button>
                </span>
            </template>
        </el-dialog>
        <!-- 用户详细信息对话框 -->
        <el-dialog title="详细信息" v-model="infoDialog.visible">
            <ul>
                <li v-for="(value,key) in infoDialog.userInfos" :key="(value,key)">
                    <span style="display:inline-block;width:60px;font-weight:bold;">{{eng2cn(key)}}</span>
                    <el-divider direction="vertical"></el-divider>{{value}}
                </li>
            </ul>
        </el-dialog>
    </div>
</template>

<script>
import {GETLIST, DEL,EDIT,GET_USER_DETAIL} from '../../api/user.js';
export default {
    name: 'UsersList',
    data() {
        return {
            currentPage:1,
            search:'',
            tableData:[],
            editDialog: {   //编辑操作对话框
                index:0, //当前操作的对象，在列表的index
                visible: false,
                labelWidth: '120px',
                formContent:{
                    StuID:'',
                    StuName: '',
                    Password:'',
                    Violation:''
                }
            },
            infoDialog: {
                visible: false,
                userInfos:{}
            }
        }
    },
    methods: {
        async getUserList() {
            GETLIST().then((res)=>{
                this.tableData = res;
            })
        },
        //处理数据，返回搜索或没有搜索的表格数据
        getProcessedData() {
            // return (this.search==''?this.tableData.slice((this.currentPage-1)*10,(this.currentPage-1)*10+10)
            //         :this.tableData.filter(data => !this.search || data.name.toLowerCase().includes(this.search.toLowerCase()))
            // )
            if(this.search==''){
                return this.tableData;
            }
            else {
                //过滤出搜索框内的数据
                return this.tableData.filter(data => ( 
                    data.StuName.toLowerCase().includes(this.search.toLowerCase())
                    ||data.StuID.toLowerCase().includes(this.search.toLowerCase())
                ));
            }
        },
        //处理点击详细按钮
        handleClickDetail(index, rowobj){
            console.log(index, rowobj);
            GET_USER_DETAIL({StuID: rowobj.StuID}).then((res)=>{
                console.log(res);
                this.infoDialog.userInfos = res;
            });
            this.infoDialog.visible = true;
        },
        eng2cn(key) {
            return ((key=='BornDate')?'出生日期':
                    (key=='Faculty')?'学院':
                    (key=='Gender')?'性别':
                    (key=='LastLogin')?'上次登录':
                    (key=='Major')?'专业':
                    (key=='Password')?'密码':
                    (key=='PhoneNumber')?'手机':
                    (key=='ReadTime')?'自习时长':
                    (key=='RestTime')?"剩余时间":
                    (key=='SeatID')?'座位号':
                    (key=='Seatlayer')?'选座楼层':
                    (key=='StuID')?'学号':
                    (key=='StuName')?'姓名':
                    (key=='UserStatus')?'用户状态':
                    (key=='Violation')?'违规次数':'请求错误')
        },
        // 处理点击编辑
        handleClickEdit(index, rowobj) {
            this.index = index;
            this.editDialog.formContent = Object.assign({},rowobj);
            this.editDialog.visible = true;
        },
        // 当点完编辑后，会弹出编辑框，处理点击编辑框中的确认按钮
        handleConfirmDialog(){
            this.editDialog.visible = false;
            this.$message({
                type: 'success',
                message: '修改成功!'
            });
            //先编辑，成功后在重新获取数据
            EDIT(this.editDialog.formContent).then(()=>{
                this.getUserList();
            });
        },
        // 处理点击删除
        handleClickDelete(index, rowobj) {
            console.log(index, rowobj);
            this.$confirm('此操作将永久删除该用户信息, 且操作不可逆，是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                // 删完重新获取数据
                DEL({StuID: rowobj.StuID}).then(()=>{
                    this.getUserList();
                });
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
            });
        },
        // 获得当前页数
        getCurrentPage(currentPage){
            this.currentPage = currentPage;
        },
        //自定义排序
        customSort(e) { //e = { column, prop, order }
            this.tableData = this.tableData.sort((m,n)=>{
                if(e.order=='ascending') return m[e.prop] - n[e.prop]; //升序
                else return n[e.prop] - m[e.prop]; //降序序
            });
            console.log(this.tableData[0]);
        }
    },
    computed:{
        
    },
    mounted() {
        this.getUserList();
    },
}
</script>

<style scoped>
    .el-table {
        width: 100%;
    }
    .el-pagination {
        margin-top: 15px;
    }
</style>