import { createRouter, createWebHashHistory } from 'vue-router';
import Login from '../components/Login/Login'
import DataShow from '../components/DataShow';
import CurrentSeats from '../components/SeatManage/CurrentSeats';
import SetRules from '../components/SeatManage/SetRules';
import UsersList from '../components/userManage/UsersList';
import BlackList from '../components/userManage/BlackList';

const routes = [
  {
    path: '/Login',
    name: 'Login',
    component: Login
  },
  {
    path: '/index',
    name: 'DataShow',
    component: DataShow
  },
  {
    path: '/SeatManage/CurrentSeats',
    name: 'CurrentSeats',
    component: CurrentSeats
  },
  {
    path: '/SeatManage/SetRules',
    name: 'SetRules',
    component: SetRules
  },
  {
    path: '/UserManage/UsersList',
    name: 'UsersList',
    component: UsersList
  },
  {
    path: '/UserManage/BlackList',
    name: 'BlackList',
    component: BlackList
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  //判断是否登录页面，如果是之前前往登录页面
  if(to.path === '/Login') {
    next();
  //如果不是登录界面，验证acessToken，如果不是我们设置的登录凭证，则跳转至登录界面
  } else if(sessionStorage.getItem('accessToken')!='canLogin'){
    next('/Login');
  //验证acessToken成功，直接放行
  } else {
    next();
  }
})

export default router
